import React, { useEffect, useState } from 'react'
import { connect } from 'react-redux'
import { useLocation } from 'react-router-dom'

import { setSearchedSerials } from './../../../store/CardList/action'

function generateNewList(posts, keyWord) {

  const key = keyWord.toLowerCase()

  if (+keyWord === 0) return null

  return posts.filter((el) => {
    if (el.name.toLowerCase().includes(key)) {
      return true
    }
    return false
  })


}

const SearchBar = (props) => {

  const dataOfSerials = props.dataOfSerials
  const [inputValue, setInputValue] = useState('')
  const [posts, setPosts] = useState(dataOfSerials.posts)
  const pathName = useLocation().pathname

  function searchSerials(posts, keyword, e) {
    e.preventDefault()
    props.setSearchedSerials(generateNewList(posts, keyword))
  }

  useEffect(() => {
    if (pathName === '/serials-tracker') {
      setPosts(dataOfSerials.posts)
    } else {
      setPosts(dataOfSerials.bookedSerials)
    }
    setInputValue('')
  }, [pathName, dataOfSerials.statusOfLoading])

  useEffect(() => {
    if (+inputValue === 0) props.setSearchedSerials(null)
  }, [inputValue])

  return (
    <div className='search'>
      <form className='search__form' role='search'
        onSubmit={(e) => searchSerials(posts, inputValue, e, props.setSearchedSerials)}
      >
        <input
          value={inputValue}
          className='search__input'
          type='search'
          name='q'
          placeholder='Search...'
          aria-label='search though site content'
          onChange={(e) => setInputValue(e.target.value)}
        />
        <button className='search__btn'><svg viewBox="0 0 1024 1024"><path className="path1" d="M848.471 928l-263.059-263.059c-48.941 36.706-110.118 55.059-177.412 55.059-171.294 0-312-140.706-312-312s140.706-312 312-312c171.294 0 312 140.706 312 312 0 67.294-24.471 128.471-55.059 177.412l263.059 263.059-79.529 79.529zM189.623 408.078c0 121.364 97.091 218.455 218.455 218.455s218.455-97.091 218.455-218.455c0-121.364-103.159-218.455-218.455-218.455-121.364 0-218.455 97.091-218.455 218.455z"></path></svg></button>
      </form>
    </div>
  )

}

const mapStateToProps = (state) => {
  return {
    dataOfSerials: state.dataOfSerials
  }
}

const mapDispatchToProps = {
  setSearchedSerials
}

export default connect(mapStateToProps, mapDispatchToProps)(SearchBar)

