import React, { useState } from 'react'
import { connect } from 'react-redux'

import { toggleNavbarStatus } from './../../../../store/Navbar/active'


const NavbarButton = (props) => {

  const [className, setClassName] = useState('')

  function toggleNavbarStatus(e) {
    e.preventDefault()
    const burger = e.target.closest('.burger')

    burger.classList.contains('active')
      ? setClassName('')
      : setClassName('active')

    props.toggleNavbarStatus(!props.navData.navStatus)

  }

  return (
    <a className={`burger ${className}`} href='#' onClick={toggleNavbarStatus}>
      <span className='burger__row'></span>
      <span className='burger__row'></span>
      <span className='burger__row'></span>
    </a>
  )
}

const mapStateToProps = (state) => {
  return {
    navData: state.navData
  }
}

const mapDispatchToProps = {
  toggleNavbarStatus
}

export default connect(mapStateToProps, mapDispatchToProps)(NavbarButton)