import React from 'react'
import bookmark from './../../../picture/icons/bookmark active.png'
import { toggleFavoriteList } from '../../../store/CardList/action'

import { connect } from 'react-redux'

const CardList = (props) => {
  const state = props.state
  const nameOfClass = !state.booked ? 'bookmark' : 'bookmark-active'

  return (
    <ul className='card__list'>
      <li className='card__item'>
        <span>
          {state.externals.thetvdb}
        </span>
        <br />Followers </li>
      <li className='card__item'>
        <span>
          {state.ended ? state.ended.substring(0, 4) : '¯_(ツ)_/¯'}
        </span>
        <br />Year</li>
      <li className='card__item'>
        <span className='moreInfo'><a href={state.url} className="moreInfo__link" >...</a></span>
        <br />More info</li>
      <li className='card__item'>
        <span>
          {state.network ? state.network.name : '¯_(ツ)_/¯'}
        </span>
        <br />Network</li>
      <li className='card__item'>
        <span>
          {state.network ? state.network.country.name : '¯_(ツ)_/¯'}
        </span>
        <br />Country</li>
      <li className='card__item'>
        <img
          src={bookmark}
          alt="Ooops... /n here must be sth"
          className={nameOfClass}
          onClick={() => props.toggleFavoriteList(state.id)}
        />
        <br />Login to folow</li>
    </ul>
  )
}

const mapStateToProps = () => {
  return {

  }
}

const mapDispatchToProps = {
  toggleFavoriteList,
}

export default connect(mapStateToProps, mapDispatchToProps)(CardList)