import React from 'react'
import { ShowInfo } from './ShowInfo/ShowInfo'

export const BookedContainer = (props) => {

  const state = props.state

  return (
    <div className='booked__container'>
      <h3 className='booked__title'> {state.name} </h3>
      <div className='booked__picture'>
        <img className='booked__img' src={state.image.medium} alt='Wow somthing goes wrong' />
      </div>
      <div className='booked__descriprion' dangerouslySetInnerHTML={{ __html: state.summary }}></div>
      <ShowInfo state={state} />
    </div>
  )
}