import React from 'react'
import moment from 'moment'

function momentMod(date, format) {
  return moment(date).format(format)
}

export const Episode = (props) => {
  const state = props.state !== 'null' ? props.state : 'null'

  return (
    <div className='episode'>
      <h6 className='episode__title'>Previous Episode</h6>
      <div className='episode__box'>
        <div className='episode__dateBox'>
          <div className='episode__mounthYear'>{momentMod(state.airdate, " MMM/DD ")}</div>
          <div className='episode__day'>{momentMod(state.airdate, "YY")}</div>
        </div>
        <h6 className='episode__name'>{state.name}</h6>
        <span className='episode__info'>Episode {state.season}x{state.number}; {momentMod(state.airdate, "MMM DD, YYYY")}</span>
        <div className='episode__description' dangerouslySetInnerHTML={{ __html: state.summary }} ></div>
      </div>
    </div>
  )
}
