import axios from 'axios'
import React, { useEffect, useState } from 'react'

import { Episode } from './Episode/Episode'
import { BookedContainer } from './BookedContainer/BookedContainer'


export const BookedBox = (props) => {
  const state = props.state
  const [prevEps, setPrevEps] = useState('null')

  useEffect(() => {
    async function fetchPrevEpsAPI() {
      let response = await axios.get(state._links.previousepisode.href)
        .then((response) => {
          return response.data
        })

      setPrevEps(response)
    }

    fetchPrevEpsAPI()
  }, [])

  return (
    <div className='booked__box' id={`serial_${props.state.id}`} >
      <BookedContainer state={state} />
      <Episode state={prevEps} />
    </div>
  )
}