import {
  REQUEST_POSTS,
  RECEIVE_POSTS,
  SET_REASERCHED_SERIALS,
  TOGGLE_FAVORITE_LIST
} from './action'

const strogeBookedList = localStorage.getItem('bookedList') // ADD FUNTIONALITY FOR UPDATE DATE ABOUT BOOKED SERIALS IN FIRST MOUNTING PAGE
const defState = {
  statusOfLoading: true,
  posts: [],
  numberForBookedSerials: !strogeBookedList ? [] : strogeBookedList.split(',').map((el) => +el),
  bookedSerials: [],
  searchedList: null
}

function changeListOfBooked(bookedList, newId) {
  if (bookedList.includes(newId)) {
    return bookedList.filter((el) => el !== newId)
  }

  localStorage.setItem('bookedList', [...bookedList, newId])
  return [...bookedList, newId]
}

function createPostList(bookedList, id) {
  return bookedList.includes(id)
}

function toggleBookedStatus(posts, bookedList) {
  return posts.map((el) => {
    if (createPostList(bookedList, el.id)) {
      return Object.assign({}, el, { booked: true })
    }

    return Object.assign({}, el, { booked: false })
  })

}

function generateBookedList(arrOfNumbers, posts) {
  const arr = []
  for (let i = 0; i < arrOfNumbers.length; i++) {
    arr.push(posts.find((el) => {

      if (el.id === arrOfNumbers[i]) {
        return el
      }
      return undefined
    }))
  }

  return arr
}


export const dataOfSerials = ((state = defState, action) => {
  switch (action.type) {
    case REQUEST_POSTS:
      return Object.assign({}, state, {
        statusOfLoading: action.statusOfLoading
      })

    case RECEIVE_POSTS:
      return Object.assign({}, state, {
        statusOfLoading: action.statusOfLoading,
        posts: JSON.parse(JSON.stringify(toggleBookedStatus(action.posts.data, state.numberForBookedSerials))),
        bookedSerials: generateBookedList(state.numberForBookedSerials, action.posts.data,)
      })
    case TOGGLE_FAVORITE_LIST:
      const numberForBookedSerials = changeListOfBooked(state.numberForBookedSerials, action.id)
      const posts = toggleBookedStatus(state.posts, numberForBookedSerials)

      return Object.assign({}, state,
        { numberForBookedSerials: numberForBookedSerials },
        { posts: JSON.parse(JSON.stringify(posts)) },
        { bookedSerials: generateBookedList(numberForBookedSerials, posts) }
      )
    case SET_REASERCHED_SERIALS:
      return Object.assign({}, state, { searchedList: action.resultOfSearching })
    default:
      break
  }

  return state
})