import { Provider } from 'react-redux'
import { createStore, applyMiddleware } from 'redux'
import thunkMiddleware from 'redux-thunk'
import {
  Routes,
  Route,
  BrowserRouter as Router,
} from 'react-router-dom'

import rootReducer from './store/reducers'
import './App.css';
import Main from './components/Main';
import BookedSerials from './components/BookedSerials/BookedSerials'
import { Header } from './components/Header/Header'

const store = createStore(rootReducer, applyMiddleware(thunkMiddleware))

function App() {
  return (
    <Provider store={store}>
      <Router>
        <Header />
        <Routes>
          <Route path='/serials-tracker' exact caseSensitive={false} element={<Main />} />
          <Route path='/serials-tracker/booked' caseSensitive={false} element={<BookedSerials />} />
        </Routes>
      </Router>
    </Provider >

  );
}

export default App;
