import React, { useState, useEffect } from 'react'
import { connect } from 'react-redux'

import Card from './Card/Card'
import { Loader } from './Loader/Loader'
import { fetchPosts } from './../store/CardList/action'
import { setSearchedSerials } from './../store/CardList/action'

function mapTheList(data, length) {
  const arr = data.map((data, index) => {

    if (index <= length) {
      return (
        <Card state={data} key={index} />
      )
    }

    return undefined
  })

  return arr
}

const Main = (props) => {

  const dataOfSerials = props.dataOfSerials,
    searchedList = dataOfSerials.searchedList,
    [lengthOfList, setLenghtOfList] = useState(50)

  const listOfCards = dataOfSerials.statusOfLoading === true
    ? <Loader />
    : mapTheList(dataOfSerials.posts, lengthOfList)

  const searchedListOfCards = !searchedList
    ? []
    : mapTheList(searchedList, lengthOfList)

  const toggleListOfCards = !props.dataOfSerials.searchedList
    ? listOfCards
    : searchedListOfCards

  function addOtherSerials(e) {

    let scrollHeight = Math.max(
      document.body.scrollHeight, document.documentElement.scrollHeight,
      document.body.offsetHeight, document.documentElement.offsetHeight,
      document.body.clientHeight, document.documentElement.clientHeight
    ) - window.innerHeight

    let html = document.documentElement,
      body = document.body,
      scrollTop = Math.round(html.scrollTop || body && body.scrollTop || 0)

    if (scrollHeight <= scrollTop && dataOfSerials.posts.length >= lengthOfList) {
      setLenghtOfList(lengthOfList + 50)
    }

  }


  useEffect(() => {
    if (dataOfSerials.statusOfLoading) {
      props.fetchPosts()
    }

    return function cleanScroll() {
      window.removeEventListener('scroll', addOtherSerials)
      props.setSearchedSerials(null)
    }
  }, [])

  useEffect(() => {
    window.removeEventListener('scroll', addOtherSerials)
    window.addEventListener('scroll', addOtherSerials);
  }, [lengthOfList, dataOfSerials.posts.length])

  return (
    <div className='wrapper' onScroll={(e) => addOtherSerials(e)}>
      <div className='main' onScroll={(e) => addOtherSerials(e)} >
        {toggleListOfCards}
      </div>
    </div >
  )
}

const mapStateToProps = (state) => {
  return {
    dataOfSerials: state.dataOfSerials
  }
}

const mapDispatchToProps = {
  fetchPosts,
  setSearchedSerials
}

export default connect(mapStateToProps, mapDispatchToProps)(Main)


