import React, { useState, useEffect } from 'react'
import { Anchor } from 'antd'
import { connect } from 'react-redux'

const { Link } = Anchor
// ADD FUNCTIONALITY FOR ADAPRIVE VERSION AND ADD STYLES FOR NAV IN MOBILE AND LESS VERSION 
const Navbar = (props) => {

  const state = props.state
  const navStatus = props.navStatus
  const [navClass, setNavClass] = useState('navbar')
  const listOfAnchorsLinks = state.map((el, index) => {
    return (
      <Link
        href={`#serial_${el.id}`}
        title={el.name}
        key={index}
      />
    )
  })

  useEffect(() => {
    !navStatus ? setNavClass('navbar') : setNavClass('navbar-active')
  }, [navStatus])

  return (
    <div className={navClass}>
      <h3 className='navbar__title' > Chose your serial:</h3>
      <Anchor offsetTop={0}>
        {listOfAnchorsLinks}
      </Anchor>
    </div >
  )
}

const mapStateToProps = (state) => {
  return {
    navStatus: state.navData.navStatus
  }
}

export default connect(mapStateToProps)(Navbar)