import React from 'react'
import { useLocation } from 'react-router-dom'

import SearchBar from './SearchBar/SearchBar'
import logo from '../../picture/logo.png'
import { FavoriteLink } from './Links/FavoriteLink'
import { MainLink } from './Links/MainLink'

export const Header = () => {
  const pathName = useLocation().pathname
  const className = pathName === 'serials-tracker' ? '' : 'header_booked'
  return (
    <header className={`header ${className}`}>
      <div className='logo'>
        <img className='logo__picture' src={logo} alt="sorry" />
      </div>
      <SearchBar />
      {pathName === '/' ? <FavoriteLink /> : <MainLink />}
    </header >
  )
}

