import axios from "axios"

export const TOGGLE_FAVORITE_LIST = 'ADD_TO_FAVORITE_LIST'
export const REQUEST_POSTS = 'REQUEST_POSTS'
export const RECEIVE_POSTS = 'RECEIVE_POSTS'
export const REQUEST_PREV_EPISODE = 'REQUEST_PREV_EPISODE'
export const RECEIVE_PREV_EPISODE = 'RECEIVE_PREV_EPISODE'
export const SET_REASERCHED_SERIALS = 'SET_REASERCHED_SERIALS'

export const toggleFavoriteList = (id) => ({
  type: TOGGLE_FAVORITE_LIST,
  id,
})

export const requestPosts = () => {

  return {
    type: REQUEST_POSTS,
    statusOfLoading: true,
  }
}

export const receivePosts = (posts) => {
  return {
    type: RECEIVE_POSTS,
    statusOfLoading: false,
    posts: posts
  }
}

export const setSearchedSerials = (resultOfSearching) => {
  return {
    type: SET_REASERCHED_SERIALS,
    resultOfSearching
  }
}

export function fetchPosts() {

  return function (dispatch) {
    dispatch(requestPosts())

    axios.get('https://api.tvmaze.com/shows')
      .then((response) => {
        return response
      })
      .then(json => {
        return dispatch(receivePosts(json))
      })
      .catch(((error) => {
        console.log(error)
      }))
  }
}