import moment from 'moment'
import React from 'react'

function makeShorterNameOfSite(link) {
  if (typeof link === 'string') {
    return link.match(/\/\/([\w_-]+(?:(?:\.[\w_-]+)+))/)[0].replace('//', '')
  }
  return '¯_(ツ)_/¯'
}

function IfItNullTest(el) {

  if (el !== null) {
    return el.name
  }
  return '¯_(ツ)_/¯'
}

export const ShowInfoList = (props) => {

  const state = props.state
  const offSite = makeShorterNameOfSite(state.officialSite)

  return (
    <ul className='showInfo__list'>
      <li className='showInfo__item'> <span className='showInfo__bold'>Network: </span> {IfItNullTest(state.network)} </li>
      <li className='showInfo__item'> <span className='showInfo__bold'>Language: </span>{state.language}</li>
      <li className='showInfo__item'> <span className='showInfo__bold'>Premiered: </span>{moment(state.premiered).format('MMM DD, YYYY')}</li>
      <li className='showInfo__item'> <span className='showInfo__bold'>Schedule: </span>{state.schedule.days} at {state.schedule.time} </li>
      <li className='showInfo__item'> <span className='showInfo__bold'>Status: </span>{state.status} </li>
      <li className='showInfo__item'> <span className='showInfo__bold'>Show Type: </span>{state.type} </li>
      <li className='showInfo__item'> <span className='showInfo__bold'>Genres: </span>{state.genres.join(', ')} </li>
      <li className='showInfo__item'> <span className='showInfo__bold'>Official site: </span><a href={`https://${offSite}`} target='_blank' rel="noreferrer" > {offSite} </a>  </li >
    </ul>
  )
}