import React, { useEffect } from 'react'
import { connect } from 'react-redux'

import { BookedBox } from './BookedBox/BookedBox'
import Navbar from './Navbar/Navbar'
import { fetchPosts } from '../../store/CardList/action'
import { setSearchedSerials } from '../../store/CardList/action'
import NavbarButton from './Navbar/NavbarButton/NavbarButton'

const BookedSerials = (props) => {

  const searchedList = props.dataOfSerials.searchedList,
    bookedSerials = props.dataOfSerials.bookedSerials,
    serials = !searchedList ? bookedSerials : searchedList,

    mapBookedList = serials.map((el, index) => {
      return <BookedBox state={el} key={index} />
    })

  useEffect(() => {
    if (props.dataOfSerials.statusOfLoading) {
      props.fetchPosts()
    }

    return function () {
      props.setSearchedSerials()
    }
  }, [])

  return (
    <div className='booked'>
      <div className='booked__list'>
        {mapBookedList}
      </div>
      <Navbar state={serials} />
      <NavbarButton />
    </div>
  )
}

const mapStateToProps = (state) => {
  return {
    dataOfSerials: state.dataOfSerials
  }
}

const mapDispatchToProps = {
  fetchPosts,
  setSearchedSerials
}

export default connect(mapStateToProps, mapDispatchToProps)(BookedSerials)
